@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@500&display=swap);
* {
  font-family: 'Ubuntu', sans-serif;
  margin: 0px;
  padding: 0px;
  align-items: center;
  justify-items: center;
  scroll-behavior: smooth;
  scroll-margin-top: 6.5em;
}

body {
  background: #001122;
}
.contact {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    text-align: center;
    letter-spacing: 0.2px;
    color: #FFFFFF;
    position: relative;
    top: 1300px;
}

.contact-header {
    font-size: 46px;
    line-height: 57px;
    margin: 10px 0px 30px;
}

.contact-desc {
    max-width: 300px;
    font-size: 14px;
    line-height: 20px;
    margin-bottom: 50px;
}

.social-icons {
    min-width: 300px;
    display: flex;
    justify-content: space-between;
    margin-bottom: 50px;
}

.address-icons {
    display: flex;
    margin: 10px;
}

.address-icons p {
    display: flex;
    flex-direction: column;
    margin: 20px;
    padding: 20px;
    max-width: 300px;
}

@media screen and (max-width: 1080px) {
    .contact {
        align-items : center;
        width: 90%;
        left: 5%;
        overflow: hidden;
    }

    .map {
        width: 95%;
    }
    
    .address-icons {
        flex-direction: column;
    }
}
.cta-container {
    display: flex;
    justify-content: space-evenly;
    letter-spacing: 0.1px;
    color: #FFFFFF;
    position: relative;
    top: 600px;
}

.cta-container img {
    max-width: 500px;
}

.cta-before {
    font-size: 22px;
    line-height: 33px;
    margin-bottom: 20px;
}

.cta-header {
    font-size: 46px;
    line-height: 57px;
}

.cta-desc {
    margin: 15px 5px;
}

.cta-group input {
    padding: 12px 30px;
    background: #EFEFEF;
    border: 1px solid #E8E8E8;
    box-sizing: border-box;
    border-radius: 39px;
    font-size: 14px;
    line-height: 16px;
    letter-spacing: 0.1px;
    color: #18171D;
}

.cta-group button {
    font-weight: bold;
    align-items: center;
    padding: 12px 30px;
    background: #6600FF;
    color: #FFFFFF;
    border-radius: 35px;
    border: 1px solid #6600FF;
    position: relative;
    left: 20px;
}

@media screen and (max-width: 1080px) {
    .cta-container {
        flex-direction: column;
    }
    .cta-container img {
        width: 80%;
    }
    .cta-group {
        margin-top: 50px;
        width: 80%;
    }
}
.feature {
    position: relative;
    top: 450px;
}

.feature-top-group,
.feature-bottom-group {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    text-align: center;
    color: #FFFFFF;
    letter-spacing: 0.2px;
}

.feature-before {
    font-size: 22px;
    line-height: 33px;
    margin-bottom: 20px;
}

.feature-heading {
    font-size: 46px;
    line-height: 57px;
}

.feature-after {
    font-size: 16px;
    line-height: 20px;
    margin-top: 20px;
}

.feature-mid-group {
    display: flex;
    letter-spacing: 0.1px;
    color: #FFFFFF;
}

.browser {
    max-width: 1000px;
    position: relative;
    left: -50px;
}

.list-heading {
    font-size: 22px;
    line-height: 33px;
    position: relative;
    top: -45px;
    left: 60px;
}

.list-desc {
    font-size: 18px;
    line-height: 27px;
    position: relative;
    top: -20px;
}

.feature-side-menu {
    position: relative;
    left: -100px;
}

.buynow {
    align-items: center;
    padding: 15px 31px;
    background: #FFFFFF;
    border: 1px solid #E8E8E8;
    border-radius: 35px;
    color: #6600FF;
    position: relative;
    left: -100px;
    top: 100px;
}

.tryfree {
    align-items: center;
    padding: 16px 45px;
    background: transparent;
    border: 1px solid #FFFFFF;
    color: #FFFFFF;
    box-sizing: border-box;
    border-radius: 35px;
    position: relative;
    top: 50px;
    left: 50px;
}

@media screen and (max-width: 1080px) {
    .feature {
        width: 90%
    }

    .feature-mid-group {
        flex-direction: column;
    }
    .browser {
        width: 90%;
        left: 0%;
    }
    .feature-side-menu {
        width: 200px;
        margin-bottom: 75px;
        left: 0%;
    }
}
.footer-end-bar {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 50px;
    background: #6600FF;
    color: #FFFFFF; 
    position: relative;
    bottom: -90rem;
}
.header {
    background-image: url(/reactJS-FigmaLandUI-SPA/static/media/bg.a273706e.svg);
    background-size: cover;
    background-repeat: no-repeat;
    color: #FFFFFF;
}

.navbar {
    display: flex;
    justify-content: space-evenly;
    position: relative;
    top: 40px;
}

.navbar a {
    font-size: 15px;
    line-height: 27px;
    text-align: center;
    letter-spacing: 0.2px;
    text-decoration: none;
    color: #FFFFFF;
}

.navbar img {
    position: relative;
    left: 45px;
    top: -10px;
}

.navbar input {
    padding: 12px 30px;
    background: #EFEFEF;
    border: 1px solid #E8E8E8;
    box-sizing: border-box;
    border-radius: 39px;
    font-size: 14px;
    line-height: 16px;
    letter-spacing: 0.1px;
    color: #18171D;
    position: relative;
    left: -20px;
}

.navbar button {
    font-weight: bold;
    align-items: center;
    padding: 12px 30px;
    background: #FFFFFF;
    color: #6600FF;
    border-radius: 35px;
    border: 1px solid #E8E8E8;
    position: relative;
    left: -50px;
}

.header-group {
    display: flex;
    flex-direction: column;
    position: relative;
    top: 130px;
}

.header-heading {
    font-weight: 900;
    font-size: 55px;
    line-height: 65px;
    text-align: center;
    letter-spacing: 0.2px;
    color: #FFFFFF;
}

.header-before {
    font-size: 22px;
    line-height: 33px;
    text-align: center;
    letter-spacing: 0.1px;
    margin-bottom: 20px;
}

.header-after {
    font-size: 16px;
    line-height: 20px;
    text-align: center;
    letter-spacing: 0.2px;
    margin-top: 20px;
}

.header-group button {
    padding: 5px 30px;
    background: #FFFFFF;
    border: 1px solid #E8E8E8;
    border-radius: 35px;
    font-size: 22px;
    line-height: 33px;
    text-align: center;
    letter-spacing: 0.1px;
    color: #6600FF;
    margin-top: 50px;
}

.header-group img {
    position: relative;
    top: 150px;
}

@media screen and (max-width: 1080px) {
    .navbar {
        flex-direction: column;
    }
    
    .navbar img {
        left: 0%;
    }

    .navbar input,
    .navbar button {
        left: 0%;
    }

    .mac {
        width: 95%;
    }
}
.partners {
    display: flex;
    flex-direction: column;
    color: #FFFFFF;
    text-align: center;
    letter-spacing: 0.2px;
    position: relative;
    top: 780px;
}

.partners-header {
    font-size: 46px;
    line-height: 57px;
    margin-bottom: 20px;
}

.partners-icon {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 50px;
}

.partners-icon img {
    width: 96px;
    margin: 0px 20px;
}

.partners button {
    font-weight: bold;
    align-items: center;
    padding: 10px 40px;
    background: #6600FF;
    color: #FFFFFF;
    border-radius: 35px;
    border: 1px solid #6600FF;
}

@media screen and (max-width: 1080px) {
    .partners-icon img {
        width: 90%;
        margin: 0px;
    }
}
.pricing {
    display: flex;
    justify-content: space-evenly;
    color: #FFFFFF;
    letter-spacing: 0.2px;
    position: relative;
    top: 1120px;
}

.pricing-header {
    font-weight: 900;
    font-size: 55px;
    line-height: 65px;
    margin-bottom: 20px;
}

.pricing-desc {
    max-width: 300px;
    word-wrap: break-word;
    margin-bottom: 20px;
}

.pricing-list {
    display: flex;
    flex-direction: column;
    font-size: 12px;
    line-height: 20px;
}

.price {
    font-weight: bold;
    font-size: 48px;
    line-height: 50px;
    margin: 20px 50px 0px 0px;
}

.month {
    position: relative;
    top: -30px;
    left: 50px;
}

.pricing button {
    font-weight: bold;
    align-items: center;
    margin-top: 20px;
    padding: 10px 40px;
    background: #6600FF;
    color: #FFFFFF;
    border-radius: 35px;
    border: 1px solid #6600FF;
}

@media screen and (max-width: 1080px) {
    .pricing {
        flex-direction: column;
        width: 90%;
        padding-left: 10px;
        margin-bottom: 50px;
    }
}
.test {
    display: flex;
    flex-direction: column;
    justify-content: center;
    position: relative;
    top: 950px;
}

.test img {
    max-width: 850px;
    margin-top: 100px;
    margin-bottom: 20px;
}

.test p {
    font-size: 46px;
    line-height: 57px;
    text-align: center;
    letter-spacing: 0.2px;
    color: #FFFFFF;
}

.test button {
    font-weight: bold;
    align-items: center;
    padding: 10px 40px;
    background: #6600FF;
    color: #FFFFFF;
    border-radius: 35px;
    border: 1px solid #6600FF;
}

@media screen and (max-width: 1080px) {
    .test img {
        width: 90%;
    }
}
